import React from "react";
import './home.css'
import ReactDOM from "react-dom";
import CanvasDraw from "react-canvas-draw";

//import { useIsMobileOrTablet } from "./utils/isMobileOrTablet";

const handleClick=()=>{
    window.location.href="/app"
}

function Home() {
  //const isMobOrTab = useIsMobileOrTablet();
    
  return (
    <div className="home-main">
      <h1>REAL TIME WEB MONITORING</h1>
      <h3>A web monitoring app which will monitor user while working online</h3>
       <h2>Features</h2>
        <p>Canvas for signing via Mouse/Finger</p>
        <p>Users Face Detection </p>
        <p>Taking a SnapShot of User every 2 mins for Records.</p>
        <p>(we can Store Image in mongoDB cloud or on local server, </p>
        <p>Image is getting Stored Locally for now)</p>
        <p>Warning User if out of frame for more than 5 mins</p>
              
      <div className="canvas-main">
      <CanvasDraw
      brushRadius='6'
        style={{
            width:"60%",
           
            boxShadow:
            "0 13px 27px -5px rgba(50, 50, 93, 0.25),    0 8px 16px -8px rgba(0, 0, 0, 0.3)"
        }}
      />
      <div  className="canvas-main-1">
      <p>Please Sign on canvas using Mouse/Finger and Start your Session</p>
      <button onClick={()=>handleClick()}> Get Started </button>
       </div>   
       </div>
    </div>
  );
}

 
export default Home



