import React, { Component } from 'react';
import Camera from './components/Camera/Camera';
import YoloResult from './components/YoloResult/YoloResult';
import { Webcam } from './webcam';
import * as tf from '@tensorflow/tfjs';
import yolo, { downloadModel } from 'tfjs-yolo-tiny';
import './App.css';
import FileSaver from 'file-saver'
import captureVideoFrame from "capture-video-frame";

let model;
let webcam;
let webcamElem;


class App extends Component {
  constructor() {
    super();
    this.state = {
      result: '',
      time:performance.now(),
      captureTime:performance.now(),
      isface:true
    }
    this.start();
  }

  async start() {
    try {
      model = await downloadModel();
      webcam = new Webcam(document.getElementById('webcam'));
      webcamElem = document.getElementById('webcam-wrapper');
      alert("Hello, This application needs to " +
        "to use you web cam. \n\nPlease Click OK to  Provide neccessary " +
        "Permission \n" +
        "This is strictly for Demo purpose.");
      await webcam.setup();
      this.run();
    } catch(e) {
      console.error(e);
    }
  }

  async run() {
    while (true) {
      this.clearRects();
       if((performance.now()-this.state.time)<300000){
       
           console.log('time for which no face detected',performance.now()-this.state.time)
           console.log('time for which no image captured',performance.now()-this.state.captureTime)
            
                
            const inputImage = webcam.capture();
           
            const t0 = performance.now();

            const boxes = await yolo(inputImage, model);

            const t1 = performance.now();
            console.log("Time Taken is" + (t1 - t0) + " milliseconds.");

            let yoloResult = '';
            boxes.forEach(box => {
                const {
                top, left, bottom, right, classProb, className,
                } = box;
                yoloResult = yoloResult + `${className} Confidence: ${Math.round(classProb * 100)}%`+'\n';
                this.drawRect(left, top, right-left, bottom-top,
                `${className} Confidence: ${Math.round(classProb * 100)}%`)
            });
            if(yoloResult.length>0){
                console.log('face detected')  
                this.setState({time:performance.now()})
            }
                
            this.setState({result: yoloResult});
            await tf.nextFrame();
            if((performance.now()-this.state.captureTime)>120000 ){
              const frame = captureVideoFrame("webcam", "png");
              console.log(frame)
              FileSaver.saveAs(frame.blob, `images_${Date.now()}.${frame.format}`);
              this.setState({captureTime:performance.now()})
            }
            
       }else{
         console.log('face Not detected for 2 mins')
        
           alert('Not Face is Detected. Please Click OK to continue')
           this.setState({time:performance.now(),captureTime:performance.now()})
       } 
      
    }
  }

   clearRects() {
    const rects = document.getElementsByClassName('rect');
    while(rects[0]) {
      rects[0].parentNode.removeChild(rects[0]);
    }
  }

   drawRect(x, y, w, h, text = '', color = 'red') {
    const rect = document.createElement('div');
    rect.classList.add('rect');
    rect.style.cssText = `top: ${y}px; left: ${x}px; width: ${w}px; height: ${h}px; border-color: ${color};`;

    const label = document.createElement('div');
    label.classList.add('label');
    label.innerText = text;
    rect.appendChild(label);

    webcamElem.appendChild(rect);
  }
  handleClick(){
    this.setState({isface:true,time:performance.now()})
  }

  render() {
  return (
    <div className="App">
        <h1>Hello User!</h1>
        <div>
              <Camera/>
              <YoloResult result={this.state.result}/>
        </div>
         
       
      </div>
    );
  }
}

export default App;



